            @click="setAnswer"
          >Ответить</el-button>
        </div>
      </template>-->
      <el-row class="q" type="flex">
        <el-col
          :md="{span: 18}"
          :lg="{span: 8}"
          class="q__block q__block--1"
          v-show="canShowStep(1)"
