                v-model="gameStep.answers[questionIndex]"
                :key="'question' + questionIndex"
                :min="0"
                :max="gameStep.maxAnswers[questionIndex]"
                class="finhistory-quest-answer"
              >
                <el-checkbox
                  v-for="(answer, answerKey) in question"
