        <!-- <button
          class="finquest-box__hint"
          @click="openHint"
          v-if="!showStepHint"
          style="bottom: 7%; right: -10%"
        >
          <span class="finquest-box__hint__name">
            <template>Нужна подсказка?</template>
          </span>
        </button>-->
        <hintBtn
          class="finquest-box__hint finhistory-hint"
          :hintCost="gameStep.isQuestion ? 1 : 5"
